export const validationProvinsi = (values) => {
  const errors = {}
  // PROVINSI
  if (!values.KodeProvinsi) {
    errors.KodeProvinsi = 'Tidak Boleh kosong'
  } else if (values.KodeProvinsi.length > 5) {
    errors.KodeProvinsi = 'Tidak Boleh Lebih Dari 5 Karakter'
  }
  if (!values.NamaProvinsi) {
    errors.NamaProvinsi = 'Tidak Boleh kosong'
  }
  // Kota
  if (!values.KodeKota) {
    errors.KodeKota = 'Tidak Boleh kosong'
  } else if (values.KodeKota.length > 5) {
    errors.KodeKota = 'Tidak Boleh Lebih Dari 5 Karakter'
  }
  if(!values.kodeProvinsi){
    errors.kodeProvinsi = "Tidak Boleh Kosong"
  } else if(values.kodeProvinsi === "DEFAULT"){
    errors.kodeProvinsi = "Silahkan Pilih Provinsi"
  }
  if(!values.NamaKota){
    errors.NamaKota = "Tidak Boleh Kosong"
  }
  // AREA
  if (!values.KodeArea) {
    errors.KodeArea = 'Tidak Boleh kosong'
  } else if (values.KodeArea.length > 5) {
    errors.KodeArea = 'Tidak Boleh Lebih Dari 5 Karakter'
  }
  if(!values.kodeKota){
    errors.kodeKota = "Tidak Boleh Kosong"
  } else if(values.kodeKota === "DEFAULT"){
    errors.kodeKota = "Silahkan Pilih Provinsi"
  }
  if(!values.NamaArea){
    errors.NamaArea = "Tidak Boleh Kosong"
  }

  //LOKASI
  if (!values.KodeLokasi) {
    errors.KodeLokasi = 'Tidak Boleh kosong'
  } else if (values.KodeLokasi.length > 5) {
    errors.KodeLokasi = 'Tidak Boleh Lebih Dari 5 Karakter'
  }
  if(!values.NamaLokasi){
    errors.NamaLokasi = 'Tidak Boleh kosong'
  }

  return errors
}
