import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationProvinsi } from "../../Validations/validationProvinsi";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { LogData } from "../../Alert";

const maptoState = (state) => {
  return {
    limit: state.config.limit,
    error: state.alert.Error,
    editProvinsi: state.provinsi.editProvinsi,
  };
};

class AreaProvinsiKota extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
      listKota: [],
      isiNama: "",
      isikode: "",
    };
  }

  componentDidMount() {
    AxiosMasterGet("provinsi/get/all")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));
    AxiosMasterGet("kota/get/all")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => LogData("Error Get Data Kota"));
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-2">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                limit={5}
                label={this.props.kode}
              />
            </div>
            {this.props.from === "kota" ? (
              <div className="col-md-3">
                <div className="form-group">
                  <Field
                    component={renderFieldSelect}
                    label={this.props.kode_ext}
                    name="kodeProvinsi"
                    value={this.props.status}
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    options={this.state.listProvinsi.map((obj) => {
                      var payload = {
                        value: obj.kode_provinsi,
                        name: obj.nama_provinsi,
                      };
                      return payload;
                    })}
                  ></Field>
                </div>
              </div>
            ) : null}
            {/* AREA */}
            {this.props.from === "area" ? (
              <div className="col-md-3">
                <div className="form-group">
                  <Field
                    component={renderFieldSelect}
                    label={this.props.kode_ext}
                    name={
                      this.props.from === "kota" ? "kodeProvinsi" : "kodeKota"
                    }
                    value={this.props.status}
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    options={this.state.listKota.map((obj) => {
                      var payload = {
                        value: obj.kode_kota,
                        name: obj.nama_kota,
                      };
                      return payload;
                    })}
                  ></Field>
                </div>
              </div>
            ) : null}
            {/* Lokasi */}
            <div className="col-md-3">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>
            {this.props.from === "area" ? (
              <div className="col-lg-3">
                <Field
                  name="kodePos"
                  component={renderField}
                  type="number"
                  label="Kode Pos"
                  placeholder="Masukan Kode Pos"
                />
              </div>
            ) : null}
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Submit
                  <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

AreaProvinsiKota = reduxForm({
  form: "Provinsi", // a unique identifier for this form
  validate: validationProvinsi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(AreaProvinsiKota);
export default connect(maptoState, null)(AreaProvinsiKota);
