import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationProvinsi } from "../../Validations/validationProvinsi";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";

const maptoState = (state) => {
  return {
    initialValues: {
      kodePos: state.provinsi.editArea.Status,
      KodeKecamatan: state.provinsi.editArea.kodeArea,
      NamaKecamatan: state.provinsi.editArea.namaArea,
      kodeKota: state.provinsi.editArea.kodeKota,
    },
  };
};
class FormAreaEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listKota: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("kota/get/all")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-2">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                readOnly={true}
                label={this.props.kode}
              />
            </div>
            <div className="col-md-4">
              <div className="form-group">
                <Field
                  component={renderFieldSelect}
                  label={this.props.kode_ext}
                  name={
                    this.props.from === "kota" ? "kodeProvinsi" : "kodeKota"
                  }
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  options={this.state.listKota.map((obj) => {
                    var payload = {
                      value: obj.kode_kota,
                      name: obj.nama_kota,
                    };
                    return payload;
                  })}
                ></Field>
              </div>
            </div>
            <div className="col-md-4">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kodePos"
                component={renderField}
                type="text"
                label="Kode Pos"
                placeholder="Masukan Kode Pos"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormAreaEdit = reduxForm({
  form: "KotaEdit", // a unique identifier for this form
  validate: validationProvinsi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(FormAreaEdit);
export default connect(maptoState, null)(FormAreaEdit);
