import React, { Component } from "react";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Table from "./Component/Table";
import { connect } from "react-redux";
import notrespond from "../Images/500.svg";
import { editarea, getareaList } from "../actions/areaAction";

import Swal from "sweetalert2";
import FormAreaEdit from "./Component/FormAreaEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalGlobal from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("kecamatan/delete/" + kode)
        .then(() => dispatch(hideModal()))
        .then((res) => berhasil("Data Berhasil Dihapus!"))
        .catch((err) => gagal("Data Gagal Dihapus!"))
        .then(() => dispatch(getareaList()));
    }
  });
}

const defaultSorted = [
  {
    dataField: "kodeArea",
    order: "asc",
  },
];

const maptoState = (state) => {
  return {
    areaList: state.provinsi.area,
    alert: state.provinsi.alert,
    onSend: state.provinsi.onSend,
    error: state.provinsi.error,
  };
};

class Area extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Area: [],
      AreaArray: [],
      response: "",
      isEdit: false,
      columns: [
        {
          dataField: "kode_kecamatan",
          text: "Kode Kecamatan",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "nama_kecamatan",
          text: "Nama Kecamatan",
          sort: true,
        },
        {
          dataField: "kode_pos",
          text: "Kode Pos",
          sort: true,
        },
        {
          dataField: "kode_kota",
          text: "Kode Kota",
          sort: true,
        },

        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_3"
                  onClick={() => hapus(this.props.dispatch, row.kode_kecamatan)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editarea(
                        row.kode_kecamatan,
                        row.nama_kecamatan,
                        row.kode_kota,
                        row.kode_pos,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getareaList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getareaList());
  };

  handleSubmit(hasil) {
    let data = {
      nama_kecamatan: hasil.NamaKecamatan,
      kode_kecamatan: hasil.KodeKecamatan,
      kode_kota: hasil.kodeKota,
      kode_pos: hasil.kodePos,
    };
    let dataEdit = {
      nama_kecamatan: hasil.NamaKecamatan,
      kode_kota: hasil.kodeKota,
      kode_pos: hasil.kodePos,
    };
    this.props.dispatch(progressSend());
    console.log(data);
    AxiosMasterPost("kecamatan/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Disimpan!")
          .then(() => this.props.dispatch(getareaList()))
          .then(() => this.props.dispatch(reset("Provinsi")))
      )
      .catch((err) => this.handleReactive(err, hasil.KodeKecamatan, dataEdit));
  }

  handleEdit(hasil) {
    const data = {
      nama_kecamatan: hasil.NamaKecamatan,
      kode_kota: hasil.kodeKota,
      kode_pos: hasil.kodePos,
    };
    this.props.dispatch(progressSend());
    // return false;
    AxiosMasterPut("kecamatan/edit/" + hasil.KodeKecamatan, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah")
          .then(() => this.props.dispatch(getareaList()))
          .then(() => this.props.dispatch(reset("Provinsi")))
      )
      .catch((err) => gagal("Data Gagal Diubah"));
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(
          err,
          kode,
          "kecamatan/reactive/",
          data,
          "kecamatan/edit/"
        ).then(() => this.props.dispatch(getareaList()))
      : gagal("Data Gagal Ditambahkan");
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kecamatan, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Kecamatan yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title=" Data Kecamatan" />
              <div className="card-body">
                <ModalGlobal
                  content={
                    this.state.isEdit ? (
                      <FormAreaEdit
                        nama="Nama Kecamatan"
                        kode="Kode Kecamatan"
                        kode_ext="Nama Kota"
                        onSubmit={(data) => this.handleEdit(data)}
                        from="area"
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <AreaProvinsiKota
                        nama="Nama Kecamatan"
                        kode="Kode Kecamatan"
                        kode_ext="Nama Kota"
                        updateStatus={this.updateStatus}
                        onSubmit={(data) => this.handleSubmit(data)}
                        from="area"
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Kecamatan"
                      : "Tambah Data Kecamatan"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Kecamatan
                    </button>
                  </div>
                </div>
                <div className="col-lg-12" data-tut="reactour_2">
                  {this.props.areaList ? (
                    <Table
                      data={this.props.areaList}
                      columns={this.state.columns}
                      defaultSorted={defaultSorted}
                      namaKey="kodeArea"
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Area);
